.login-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url('https://images-assets.nasa.gov/image/PIA03606/PIA03606~large.jpg?w=1920&h=1920&fit=crop&crop=faces%2Cfocalpoint');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  position: relative;
}

.login-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 13, 28, 0.5);
  backdrop-filter: blur(0px);
}

.login-card {
  background: rgba(13, 16, 33, 0.9);
  padding: 2.5rem;
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(99, 179, 237, 0.3);
  width: 100%;
  max-width: 400px;
  position: relative;
  border: 1px solid rgba(99, 179, 237, 0.2);
  z-index: 1;
}

.login-title {
  color: #e0e7ff;
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0 0 10px rgba(99, 179, 237, 0.5);
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  color: #63b3ed;
  font-size: 1rem;
  font-weight: 500;
  text-shadow: 0 0 5px rgba(99, 179, 237, 0.3);
}

.form-input {
  padding: 0.8rem 1rem;
  border-radius: 8px;
  border: 1px solid rgba(99, 179, 237, 0.3);
  background: rgba(13, 16, 33, 0.8);
  color: #e0e7ff;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-input:focus {
  outline: none;
  border-color: #63b3ed;
  box-shadow: 0 0 10px rgba(99, 179, 237, 0.4);
}

.login-button {
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 8px;
  border: none;
  background: linear-gradient(135deg, #4299e1, #2b6cb0);
  color: white;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.login-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(99, 179, 237, 0.4);
}

.error-message {
  color: #fc8181;
  text-align: center;
  margin-top: 0.5rem;
  font-size: 0.9rem;
  text-shadow: 0 0 5px rgba(252, 129, 129, 0.3);
}

@media (max-width: 768px) {
  .login-card {
    margin: 1rem;
    padding: 1.5rem;
  }
  
  .login-title {
    font-size: 1.75rem;
  }
}